<template>
	<div id="statcenter-index">
		<public-header title="商户统计"></public-header>
		<div class="content">
			<van-cell-group class="margin-10-t">
				<van-cell is-link :to="util.getUrl({path: '/pages/statcenter/order'})">
					<div class="flex" slot="title">
						<i class="icon icon-data font-16 margin-5-r c-info"></i>
						<span>营业统计</span>
					</div>
				</van-cell>
				<van-cell is-link :to="util.getUrl({path: '/pages/statcenter/goods'})">
					<div class="flex" slot="title">
						<i class="icon icon-goods font-18 margin-5-r c-danger"></i>
						<span>热门商品</span>
					</div>
				</van-cell>
			</van-cell-group>
			<van-cell-group class="margin-10-t">
				<van-cell is-link :to="util.getUrl({path: '/pages/statcenter/newAndOld'})">
					<div class="flex" slot="title">
						<i class="icon icon-person2 font-18 margin-5-r c-primary"></i>
						<span>新老顾客统计</span>
					</div>
				</van-cell>
				<van-cell is-link :to="util.getUrl({path: '/pages/statcenter/scan'})">
					<div class="flex" slot="title">
						<i class="icon icon-people3 font-18 margin-5-r c-primary"></i>
						<span>客流分析</span>
					</div>
				</van-cell>
				<van-cell is-link :to="util.getUrl({path: '/pages/statcenter/keep'})">
					<div class="flex" slot="title">
						<i class="icon icon-people font-18 margin-5-r c-primary"></i>
						<span>顾客留存统计</span>
					</div>
				</van-cell>
			</van-cell-group>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	export default {
		components: {
			publicHeader
		},
		data() {
			return {
				showPreLoading: true
			}
		},
		mounted(){
			this.showPreLoading = false;
		}
	}
</script>

<style scoped>

</style>


